export default {
    name: "graceNumberBox",
    props: {
        disabled: {
            type: Boolean,
            default: false
        },
        value: {
            type: Number,
            default: 0
        },
        index: {
            type: Number,
            default: 0
        },
        maxNum: {
            type: Number,
            default: 20
        },
        minNum: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            inputNumber: this.value
        }
    },
    watch: {
        value(val) {
            this.inputNumber = val;
        },
        inputNumber(val) {
            if (val > this.maxNum) {
                val = this.maxNum;
                this.inputNumber = val;
            }
            if (val < this.minNum) {
                val = this.minNum;
                this.inputNumber = val;
            }
            this.$emit('change', [val, this.index]);
        }
    },
    methods: {
        add: function() {
            var newVal = this.inputNumber + 1;
            this.inputNumber = Number(newVal);
        },
        reduce: function() {
            var newVal = this.inputNumber - 1;
            this.inputNumber = newVal;
        },
        blur: function(e) {
            this.inputNumber = Number(e.detail.value);
        }
    }
}
